import { Component } from '@angular/core';

import { MenuController } from '@ionic/angular';
import { AuthenticationService } from '../DESARROLLO/services/Authentication.service';

@Component({
  selector: 'app-tabs',
  templateUrl: 'tabs.page.html',
  styleUrls: [
    './styles/tabs.page.scss'
  ]
})
export class TabsPage  {
 inventario = true;
 totalpay: String = '';
 totalmarket: String = '';
 formspay: String = '';
 procesopay: String = '';
rolex:any;
 t1 = 0;
 t2 = 0;
 t3 = 0;
  constructor(public menu: MenuController,  private authenticationService: AuthenticationService ) {

    this.totalpay = this.authenticationService.getsibilletera();
    this.totalmarket = this.authenticationService.getsimarket();
    this.formspay = this.authenticationService.getformularios();
    this.procesopay = this.authenticationService.getprocess();
  }
  userinfo: any = [];
  ngOnInit() {

    this.userinfo = this.authenticationService.getcopiauser();

    this.rolex=this.userinfo[0].role;

    if (this.userinfo[0].role === 'Administrador' || this.userinfo[0].role === 'Inventario admin') {this.inventario = false;

    } else {
      if (this.userinfo[0].role === 'Inspector planificación' ) {this.t1 = 1;} else {
        if (this.userinfo[0].role === 'Supervisor planificación' ) {this.t2 = 1;} else {
        this.t3 = 1;
      }
      }

    }
  }
  ionViewWillEnter() {
    this.menu.enable(true);
   // document.querySelector('#tab-button-tab3').shadowRoot.querySelector('.button-native').setAttribute('style', 'margin-top: -2px');
  }
}
